@media only screen and (max-width: 768px) {

.App {

}

body, html {
 min-height: -webkit-fill-available;
 padding-bottom: env(safe-area-inset-bottom);
 background-color: #050505;
 overflow: hidden;
 overscroll-behavior-y: none !important;
}


::-webkit-scrollbar {
  display: none;
}

a {
  color: #050505;
  text-decoration: none; /* no underline */
}
a:hover {
  opacity: .4;
}
a:visited {
  opacity: .8;
}

.row {
  display: flex;
  justify-content: center;
  flex-direction: row;
}
.col-1 {
  display: flex;
  flex: 1;
  align-items: center;

}
.col-2 {
  display: flex;
  flex: 2;
  align-items: center;
}
.col-3 {
  display: flex;
  flex: 3;
  align-items: center;
}
.col-4 {
  display: flex;
  flex: 4;
  align-items: center;
}


 .top-spacer {
  width: 100%;
  padding-top: 80px;
}
 .bottom-spacer {
  width: 100%;
  padding-top: 60px;
}


.title {
  margin: 0;
  /* font-size: 3.8vw; */
  font-size: 28px;
  white-space: wrap;

}


.subtitle {
 
  font-size: 16px;
  margin: 0;
  margin-bottom: 2vh;
  

}
.text {
  font-size: 16px;
  color: #959595;
  line-height: 1.4em;
}



h1 {

}
h2 {
  font-family: Inter;
  font-weight: 300;
  /* font-size: 36px; */
}
h6 {
  font-family: Inter;
  font-weight: 300;
  /* font-size: 18px; */
}
p {
  font-family: Inter;
  font-weight: 300;
  font-size: 16px;
  font-weight: 400;
}




  .row {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
}